html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
::before,
::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 1.6rem;
  background: var(--color-yellow);
  color: var(--color-grey);
  height: 100vh;
  font-family: var(--font-figtreeSBold);
}

@font-face {
  font-family: "Figtree-ExtraBold";
  src: url(/dist/fonts/static/Figtree-ExtraBold.ttf);
}
@font-face {
  font-family: "Figtree-SemiBold";
  src: url(/dist/fonts/static/Figtree-SemiBold.ttf);
}
:root {
  --font-figtreeXBold: "Figtree-ExtraBold";
  --font-figtreeSBold: "Figtree-SemiBold";
}

.container {
  min-height: 100dvh;
  display: grid;
  justify-content: center;
  align-items: center;
}

:root {
  --color-yellow: hsl(47, 88%, 63%);
  --color-white: hsl(0, 0%, 100%);
  --color-grey: hsl(0, 0%, 50%);
  --color-black: hsl(0, 0%, 7%);
}

.card {
  display: flex;
  flex-direction: column;
  background-color: var(--color-white);
  max-width: 36rem;
  gap: 2rem;
  padding: 2.5rem 2.5rem;
  border: 1px solid var(--color-black);
  border-radius: 1.8rem;
  box-shadow: var(--color-black) 0.8rem 0.8rem;
  transition: all 200ms ease-out;
}
.card img {
  border-radius: 1rem;
}
.card:hover {
  box-shadow: var(--color-black) 1.2rem 1.2rem;
}
.card__subtitle {
  border-radius: 0.4rem;
  width: 6rem;
  background-color: var(--color-yellow);
  color: var(--color-black);
  font-size: 1rem;
  font-weight: bold;
  padding: 0.5rem 1rem;
}
.card__date {
  font-size: 1.2rem;
  color: var(--color-black);
}
.card__title {
  font-size: 2.4rem;
  color: var(--color-black);
  font-family: var(--font-figtreeXBold);
  font-weight: bold;
  cursor: pointer;
}
.card__title:hover {
  color: var(--color-yellow);
}
.card__description {
  color: var(--color-grey);
  font-size: 1.4rem;
}
.card__profile {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  font-size: 1.2rem;
  color: var(--color-black);
  font-weight: bold;
}
.card__profile img {
  width: 3rem;
  height: 3rem;
}